import LinkResolver from "~/plugins/link-resolver.js";

export default {
  props: ["post"],
  name: "posts",
  data: function() {
    return {
      link: "",
      formattedDate: ""
    };
  },
  created() {
    (this.link = LinkResolver(this.post)),
      (this.formattedDate = Intl.DateTimeFormat("en-US", {
        year: "numeric",
        month: "short",
        day: "2-digit"
      }).format(new Date(this.post.data.date)));
  }
};
